<template>
  <v-row justify="center">
    <v-col cols="12" sm="12" md="10" lg="8">
      <v-card ref="form">
        <v-card-title>{{ title }}</v-card-title>
        <v-card-text>
          <v-row>
            <v-col cols="12" sm="7">
              <v-row>
                <v-col>
                  <v-text-field ref="name" prepend-icon="mdi-account-box " v-model="name"
                    :rules="[() => !!name || 'Campo requerido']" :error-messages="errorMessages"
                    v-on:input="[changeValue()]" label="Nombre" autofocus required></v-text-field>
                </v-col>
                <v-col>
                  <v-text-field ref="lastname" v-model="lastname" :rules="[() => !!lastname || 'Campo requerido']"
                    :error-messages="errorMessages" label="Apellido" v-on:input="[changeValue()]" required>
                  </v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col>
                  <v-text-field ref="company" prepend-icon="mdi-domain" v-model="company"
                    :error-messages="errorMessages" label="Empresa" v-on:input="[changeValue()]" required>
                  </v-text-field>
                </v-col>
                <v-col>
                  <v-menu :close-on-content-click="true" :nudge-right="40" transition="scale-transition" offset-y
                    min-width="auto">
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field ref="date" @blur="[(date = parseDate(dateFormatted))]" v-model="dateFormatted"
                        label="Fecha estimada de entrega" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on">
                      </v-text-field>
                    </template>
                    <v-date-picker v-model="date" no-title :min="new Date().toISOString().slice(0, 10)"
                      :first-day-of-week="1" locale="es-es" v-on:input="[changeValue()]" @input="menu2 = false">
                    </v-date-picker>
                  </v-menu>
                </v-col>
              </v-row>

              <v-row>
                <v-col cols="6">
                  <v-text-field ref="email" v-on:input="[changeValue()]" prepend-icon="mdi-card-account-mail "
                    v-model="email" :error-messages="errorMessages" :rules="[
                      (v) =>
                        !!v ||
                        /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) ||
                        'E-mail debe ser válido',
                    ]" label="E-mail" required></v-text-field>
                </v-col>
                <v-col cols="6">
                  <v-text-field ref="phone" :error-messages="errorMessages" prepend-icon="mdi-card-account-phone"
                    v-model="phone" :rules="[() => !!phone || 'Campo requerido']" label="Teléfono"
                    v-on:input="[changeValue()]" required></v-text-field>
                </v-col>
              </v-row>
            </v-col>
            <v-col cols="12" sm="5">
              <v-row>
                <v-textarea prepend-icon="mdi-message-bulleted " label="Comentarios" v-model="notes"
                  v-on:input="[changeValue()]" ref="notes" rows="8" value=""></v-textarea>
              </v-row>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
// import axios from "axios";

export default {
  data: () => ({
    errorMessages: "",
    name: "",
    lastname: "",
    phone: "",
    date: "",
    email: "",
    company: "",
    notes: "",
    formHasErrors: false,
    dateFormatted: "",
  }),

  computed: {
    computedDateFormatted() {
      return this.formatDate(this.date);
    },
    form() {
      let contactForm = {
        name: this.name,
        lastname: this.lastname,
        phone: this.phone,
        date: this.formatDate(this.date),
        email: this.email,
        company: this.company,
        notes: this.notes,
      };
      return contactForm;
    },
  },
  props: ["title", "contact"],
  watch: {
    name() {
      this.errorMessages = "";
    },
    date() {
      this.dateFormatted = this.formatDate(this.date);
    },
  },

  methods: {
    changeValue() {
      this.formHasErrors = false;
      Object.keys(this.form).forEach((f) => {
        if (!this.form[f]) this.formHasErrors = true;

        this.$refs[f].validate(true);
      });

      this.$emit("changeValid", this.formHasErrors);

      this.$emit("changeValue", this.form);
    },
    formatDate(date) {
      if (!date) return "";

      const [year, month, day] = date.split("-");
      return `${day}/${month}/${year}`;
    },
    parseDate(date) {
      if (!date) return "";

      const [month, day, year] = date.split("/");
      return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
    },
  },
};
</script>
