<template>
  <v-row justify="center">
    <v-col cols="12" sm="12" md="10" lg="8">
      <v-card ref="form">
        <v-card-title>{{ title }}</v-card-title>
        <v-card-text>
          <v-card class="mx-auto" max-width="600" tile>
            <v-list>
              <v-list-group v-for="(option, n) in options" :key="'FirstStepItem' + n" no-action>

                <template v-slot:activator>
                  <v-list-item-icon>
                    <v-icon>{{ option.icon }}</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title>{{ option.name }}</v-list-item-title>
                  </v-list-item-content>
                </template>

                <!-- <v-list-item-group v-model="selectedItem" color="primary"> -->
                <v-list-item v-model="selectedItem" v-for="(option, n) in option.child" :key="'SecondStepItem' + n"
                  @click="[changeValue(option.name)]">
                  <v-list-item-icon>
                    <v-icon>{{ option.icon }}</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title>{{ option.name }}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <!-- </v-list-item-group> -->

              </v-list-group>
            </v-list>
          </v-card>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: "child-first",
  props: ["options", "title"],
  data() {
    return {
      selectedItem: 0,
    };
  },
  methods: {
    changeValue(value) {
      this.$emit("changeValue", value);
    },
  },
};
</script>
