<style scoped>
.v-stepper--is-booted .v-stepper__content,
.v-stepper--is-booted .v-stepper__wrapper {
  transition: 1s cubic-bezier(0.25, 0.8, 0.5, 1);
}
</style>

<template>
  <div>
    <v-stepper v-model="curr">
      <v-stepper-header class="overflow-x-auto">
        <template v-for="(step, n) in steps">
          <v-stepper-step :key="`${n}-step`" :complete="stepComplete(n + 1)" :step="n + 1"
            :rules="[(value) => !!step.valid]" :color="stepStatus(n + 1)">
            {{ step.name }}

            <small v-if="step.optional">Opcional</small>
          </v-stepper-step>

          <v-divider v-bind:key="n" v-if="n < steps.length - 1" />
        </template>
      </v-stepper-header>
      <v-stepper-items>
        <v-stepper-content step="1">
          <FirstStep :title="steps[0].name" @changeValue="[(steps[0].value = $event), validate(0)]"
            :options="steps[0].options" />


          <v-btn class="float-right" color="primary" @click="validate(0)" :disabled="!steps[0].valid">Continuar</v-btn>

        </v-stepper-content>

        <v-stepper-content step="2">
          <ThirdStep :active="curr == 2" :title="steps[1].name" :products="steps[1].products"
            @changeValue="[(steps[1].value = JSON.stringify($event)), (validate(1, false))]" />

          <v-btn class="float-right" color="primary" @click="validate(1)" :disabled="!steps[1].valid">Continuar</v-btn>
          <v-btn text @click="[sendEvent('step_back_' + curr), (curr -= 1)]"
            v-if="curr <= lastStep && curr > 1">Volver</v-btn>
        </v-stepper-content>

        <v-stepper-content step="3">
          <FourStep :title="steps[2].name" @changeValue="[(steps[2].value = $event)]" />

          <v-btn class="float-right" color="primary" @click="validate(2)" :disabled="!steps[2].valid">Continuar</v-btn>
          <v-btn text @click="[sendEvent('step_back_' + curr), (curr -= 1)]"
            v-if="curr <= lastStep && curr > 1">Volver</v-btn>
        </v-stepper-content>

        <v-stepper-content step="4">
          <FiveStep :contact="steps[3].value" :title="steps[3].name" @changeValue="steps[3].value = $event"
            @changeValid="steps[3].valid = $event" />

          <v-btn text @click="[sendEvent('step_back_' + curr), (curr -= 1)]"
            v-if="curr <= lastStep && curr > 1">Volver</v-btn>
          <v-btn class="float-right" color="success" @click="[done(), submit(steps[3]), loader = 'loading']"
            :loading="loading" :disabled="loading">Enviar</v-btn>

        </v-stepper-content>

      </v-stepper-items>
    </v-stepper>

    <v-dialog v-model="loadingModal" max-width="500px" :key="'modalloader'">
      <v-skeleton-loader width="500px" type="card"></v-skeleton-loader>
    </v-dialog>

    <v-dialog v-model="dialog" max-width="500px" :key="'modal1'" persistent>

      <v-card>
        <v-card-title>
          <span class="text-h5">Solicitud enviada correctamente</span>
        </v-card-title>
        <v-card-text>
          <v-icon x-large color="green">mdi-check-circle</v-icon> El formulario se envio correctamente
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <!-- <v-btn color="green darken-1" text @click="dialog = false">
            OK
          </v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialogError" max-width="500px" :key="'modal2'">

      <v-card>
        <v-card-title>
          <span class="text-h5">Solicitud no enviada</span>
        </v-card-title>
        <v-card-text>
          <v-icon x-large color="red">mdi-alert-circle</v-icon> Hubo un error al enviar el formulario
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="dialogError = false">
            OK
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </div>
</template>

<script>
import FirstStep from "./FirstStep.vue";
import ThirdStep from "./ThirdStep.vue";
import FourStep from "./FourStep.vue";
import FiveStep from "./FiveStep.vue";


export default {
  data: () => ({
    loader: null,
    loading: false,
    dialogError: false,
    dialog: false,
    loadingModal: false,
    curr: 1,
    lastStep: 4,
    steps: [
      {
        name: "Tipo de obra",
        rules: [(v) => (v && v.length >= 4) || false],
        value: "",
        valid: true,
        options: [
          {
            name: "Obra adjudicada", icon: "mdi-account-hard-hat",
            child: [
              {

                name: "Ejecución",
                icon: "mdi-office-building-cog",
              },
              {

                name: "Explotación / Mantenimiento",
                icon: "mdi-office-building",
              }

            ]
          },
          {
            name: "Obra en estudio", icon: "mdi-calendar-search",
            child: [
              {

                name: "Estudio previo",
                icon: "mdi-chair-rolling",
              },
              {

                name: "Anteproyecto",
                icon: "mdi-chair-rolling",
              },
              {

                name: "Redacción de proyecto",
                icon: "mdi-chair-rolling",
              },
              {

                name: "Licitación",
                icon: "mdi-chair-rolling",
              }
            ]
          },
        ],
      },
      {
        name: "Productos",
        rules: [(v) => (v && v.length >= 4) || false],
        value: "",
        valid: true,
        products: [
          { id: 'h8', name: "Hormigones Livianos: H8", units: ["m3"] },
          { id: 'h13', name: "Hormigones Livianos: H13", units: ["m3"] },
          { id: 'h17', name: "Hormigones Livianos: H17", units: ["m3"] },

          { id: 'h21', name: "Hormigones Estructurales: H21", units: ["m3"] },
          { id: 'h25', name: "Hormigones Estructurales: H25", units: ["m3"] },
          { id: 'h30', name: "Hormigones Estructurales: H30", units: ["m3"] },

          { id: 'rdc', name: "RDC (Relleno de densidad controlada)", units: ["m3"] },

          { id: 'h21', name: "Hormigones Gunitado: H21", units: ["m3"] },
          { id: 'h30', name: "Hormigones Gunitado: H30", units: ["m3"] },

          { id: 'hac', name: "Hormigon Autocompactante HAC", units: ["m3"] }
        ],
      },
      { name: "Dirección de la obra", rules: [() => true], valid: true, value: "", optional: true },
      {
        name: "Contacto",
        valid: true,
        value: {
          name: "",
          lastname: "",
          phone: "",
          date: "",
          email: "",
          company: "",
          notes: "",
        },
        rules: [
          (v) =>
            (v && v.name != "" && v.phone != "" && v.email != "") ||
            "Enter at least 4 characters.",
        ],
      }
    ],
    valid: false,
    stepForm: [],
  }),
  methods: {
    sendEvent(label) {
      this.$gtm.trackEvent({
        category: 'Cotizador',
        action: 'click',
        label: label,
        value: 1
      });
    },
    submit(step) {
      this.loadingModal = true;

      if (step.valid == false) {
        console.log("No puedo enviar formulario");
        this.loadingModal = false;
        this.dialogError = true;
        return false;
      }
      this.formHasErrors = false;

      const scriptURL =
        "https://script.google.com/macros/s/AKfycbzfhVIHoIHqvyor8DlDtlEAoHAuUggah3gAYwMz8gMaK8j6b8e6vMVt65F1_VjfNGi7/exec";

      let formData = new FormData();
      this.steps.forEach((step) => {
        if (typeof step.value === "object") {
          for (let x in step.value) {
            formData.append(x, step.value[x]);
          }
        } else {
          formData.append(step.name, step.value);
        }
      });

      fetch(scriptURL, { method: "POST", body: formData })
        .then(() => {
          console.log("Success!");
          this.loadingModal = false;
          this.dialog = true;

          this.sendEvent('send_form');
        })
        .catch((error) => {
          this.loadingModal = false;
          this.dialog = false;
          this.dialogError = true;
          console.error("Error!", error.message);

          this.sendEvent('send_form_fail');

        });
    },
    stepComplete(step) {
      return this.curr > step;
    },
    stepStatus(step) {
      return this.curr > step ? "green" : this.$vuetify.theme.themes.light.primary;
    },
    validate(n, move = true) {
      this.steps[n].valid = false;
      let v = this.steps[n].rules[0](this.steps[n].value);
      if (v) {

        this.sendEvent('step_valid_' + n);

        this.steps[n].valid = true;
        if (move) {
          this.curr = n + 2;
        }
      } else {
        this.sendEvent('step_invalid_' + n);
      }
    },
    done() {
      this.curr = this.lastStep;
    },
  },
  watch: {
    loader() {
      const l = this.loader
      this[l] = !this[l]

      setTimeout(() => (this[l] = false), 3000)

      this.loader = null
    },
  },
  components: { FirstStep, ThirdStep, FourStep, FiveStep },
  mounted() {
    this.$gtm.trackView('View Cotizador - MainStepper', '/cotizador');
  },
};
</script>
