<template>
  <v-row justify="center">
    <v-col cols="12" sm="12" md="10" lg="8">
      <v-card>
        <v-card-title>{{ title }}</v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="6">
                <!-- <v-responsive :aspect-ratio="4 / 3">
                  <div ref="map-root" v-bind:style="{ width: '100%', height: '100%' }"></div>
                </v-responsive> -->
                <template>
                  <l-map style="height: 300px" :options="{ attributionControl: false }" :zoom="9" :center="center">
                    <l-tile-layer :url="urlgm"></l-tile-layer>
                    <l-marker :lat-lng="marker"></l-marker>
                    <l-geo-json :optionsStyle="styleOption" :geojson="geojson" />
                  </l-map>
                </template>
              </v-col>
              <v-col cols="12" sm="6">
                <v-row>
                  <v-col>
                    <v-here-geocoder-autocomplete :here-api-key="myHereApiKey" v-model="address" ref="address"
                      in="countryCode:ARG" at="-58.85371727625321,-34.61419110126904" hide-no-data
                      :rules="[() => !!address || 'Campo requerido', addressCheck]" label="Dirección de la obra"
                      lang="es-AR" debounce-time clearable />

                  </v-col>
                </v-row>
                <v-row>
                  <v-col>
                    <v-text-field ref="city" prepend-icon="mdi-map-marker" v-model="city" label="Ciudad"></v-text-field>
                  </v-col>
                  <v-col>
                    <v-text-field ref="state" prepend-icon="mdi-map-marker" v-model="state"
                      label="Provincia"></v-text-field>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import 'leaflet/dist/leaflet.css';

import { LMap, LTileLayer, LMarker, LGeoJson } from 'vue2-leaflet';

import VHereGeocoderAutocomplete from "vuetify-here-geocoder-autocomplete";

export default {
  name: "MapContainer",
  components: {
    VHereGeocoderAutocomplete, LMap,
    LTileLayer,
    LMarker, LGeoJson
  },
  props: ["title"],
  async created() {
    const response = await fetch('./layer.geojson');
    this.geojson = await response.json();
  },
  methods: {
    styleOption(feature) {
      switch (feature.properties.AA_MINS) {
        case 15: return { color: "#4596e6", opacity: "10", weight: "1.25" };
        case 30: return { color: "#7facd9", opacity: "10", weight: "1.25" };
        case 45: return { color: "#b8c2cc", opacity: "15", weight: "1.25" };
        case 60: return { color: "#a29f98", opacity: "70", weight: "1" };
        default: return { color: "#767064", opacity: "90", weight: "1" };
      }

    },
    changeValue(value) {
      this.$emit("changeValue", JSON.stringify(value));
    },
    addressCheck() {
      this.errorMessages =
        this.address && !this.name ? `Hey! I'm required` : "";

      this.city = this.address?.address.city
      this.state = this.address?.address.state

      this.changeValue(this.address?.address)

      return true;
    },
  },
  data: () => ({
    geojson: null,
    urlgm: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
    center: [-34.61419110126904, -58.85371727625321],
    marker: [-34.61419110126904, -58.85371727625321],
    errorMessages: "",
    address: null,
    city: null,
    state: null,
    zip: null,
    place: null,
    myHereApiKey: "9l5XwXsm0ds0ChqERsb4JZtU9Gc_9ul68l4mioL6JAo",
  }),
};
</script>
